.paging {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  width: 100%;
  font-weight: 600;

  > * {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 5px 5px;
    width: 46px;
    height: 46px;
    color: #8A8A8A;
    background: #FFFFFF;
    border: 1px solid #DFDFDF;
    border-radius: 4px;

    &:hover {
      background: darken(#fff, 4%);
    }

    &.current {
      width: 50px;
      height: 50px;
      color: #ffffff;
      background: #86BE5C;
    }
  }

  .next, .prev {
    &:hover {
      color: darken(#86BE5C, 10%);
    }
  }

  .prev {
    margin-right: 20px;
  }

  .next {
    margin-left: 20px;
  }
}

@include md-down {
  .paging {
    margin-top: 40px;

    > * {
      margin: 0 4px 5px;
      width: 32px;
      height: 32px;
      font-size: 0.75rem;

      &.current {
        width: 36px;
        height: 36px;
      }
    }

    .prev {
      margin-right: 12px;
    }

    .next {
      margin-left: 12px;
    }
  }
}