.partners {
  .container_section {
    &-wrapper {
      padding: 40px 5px 20px;
    }
  }
  &__items {
    display: flex;
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 33.33%;
    margin-bottom: 100px;
    cursor: pointer;
    &:hover {
      .partners__item {
        &__info {
          opacity: 1;
          visibility: visible;
          transition: all 0.3s linear;
        }
      }
    }
    &__info {
      opacity: 0;
      visibility: hidden;
      padding: 30px 35px 20px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -40%);
      width: 100%;
      font-weight: 500;
      background: rgba(#f8f8f8, 0.85);
      border-radius: 6px;
      z-index: 1;
      span {
        font-size: 1.125rem;
        font-weight: 700;
      }
    }
  }
}

@include md-down {
  .partners {
    .container_section {
      &-wrapper {
        padding-top: 35px;
        padding-bottom: 45px;
      }
    }
    &__item {
      margin-bottom: 50px;
      &:last-of-type {
        margin-bottom: 0;
      }
      width: 100%;
      &__info {
        position: relative;
        padding: 0 10px 0 0;
        left: auto;
        top: auto;
        transform: translate(0, 0);
        margin-top: 10px;
        opacity: 1;
        visibility: visible;
        background: none;
      }
    }
  }
}

@include sm-down {
  .partners {
    &__items {
      flex-direction: column;
      align-items: center;
    }
  }
}

.letters {
  background: #f8f8f8;
  &__desc {
    font-size: 1.125rem;
    font-weight: 500;
    line-height: 1.5;
  }
  &__items {
    &-wrapper {
      margin-top: 45px;
    }
  }
}

@include md-up {
  .letters {
    &__items {
      display: flex;
      justify-content: space-between;
      &-wrapper {
        .slider__arrow_main {
          display: none;
        }
      }
    }
    &__item {
      margin-bottom: 40px;
      width: calc(33.33% - 57px);
    }
  }
}

@include md-down {
  .letters {
    &__items {
      margin-top: 30px;
      &-wrapper {
        margin-top: 30px;
        padding-bottom: 60px;
      }
    }
    &__items {
      &-wrapper {
        margin-left: -20px;
        .slick {
          &-slide {
            margin-left: 20px;
          }
        }
      }
    }
    &__item {
      img {
        margin: auto;
      }
    }
  }
}