.map-wrapper {
  position: relative;
  width: 100%;
  height: 400px;
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    z-index: 1;
  }
  #map {
    width: 100%;
    height: 100%;
  }
}

@include sm-down {
  .map-wrapper {
    height: 320px;
  }
}