.pg-reviews {
  .section {
    &-banner {
      background: url("../img/bg/bg-reviews.png") 50% 100% no-repeat;
      background-size: auto auto;
    }
  }
  &__st {
    &__item {
      margin-bottom: 70px;
      &:last-of-type {
        margin-bottom: 0;
      }
      &__head {
        display: flex;
        &__img-wrapper {
          width: 85px;
          height: 45px;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
            object-fit: contain;
          }
        }
        &__title {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          padding-left: 30px;
          h3 {
            font-size: 1.125rem;
            font-weight: 500;
          }
          &__sender {
            span {
              margin-left: 29px;
            }
          }
        }
        &__status {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          margin-left: auto;
          &__date {
            font-size: 0.875rem;
            color: #8a8a8a;
          }
        }
      }
      &__text {
        margin-top: 15px;
        font-size: 1.125rem;
        font-weight: 500;
        line-height: 1.5;
      }
      &__sender_df {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        span {
          color: #8a8a8a;
          font-size: 0.9375rem;
        }
      }
      &__inner-review {
        padding: 38px 5px 0 22px;
        &__head {
          display: flex;
          &__img-wrapper {
            width: 104px;
            height: 31px;
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
          &__title {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding-left: 20px;
            margin-top: -4px;
          }
          &__sender {
            font-size: 1.125rem;
            font-weight: 500;
            line-height: 1;
          }
          &__date {
            margin-top: 4px;
            font-size: 0.875rem;
            color: #8a8a8a;
          }
        }
      }
    }
  }
}

@include md-down {
  .pg-reviews {
    &__st {
      &__item {
        margin-bottom: 50px;
        &__head {
          &__img-wrapper {
            width: 75px;
            height: 40px;
          }
          &__title {
            padding-left: 25px;
            h3 {
              font-size: 1.125rem;
            }
          }
        }
        &__text {
          margin-top: 15px;
          font-size: 1rem;
        }
        &__inner-review {
          padding: 25px 5px 0 20px;
          &__head {
            &__img-wrapper {
              width: 95px;
            }
            &__title {
              padding-left: 15px;
            }
            &__sender {
              font-size: 1rem;
            }
          }
        }
      }
    }
  }
}

@include sm-up {
  .pg-reviews {
    &__st {
      &__item {
        &__sender_df {
          display: none;
        }
        &__status {
          display: none;
        }
      }
    }
  }
}

@include sm-down {
  .pg-reviews {
    &__st {
      &__item {
        margin-bottom: 45px;
        &__head {
          &__img-wrapper {
            width: 52px;
            height: 30px;
          }
          &__title {
            justify-content: center;
            padding-left: 10px;
            width: calc(100% - 52px);
            h3 {
              font-size: 0.9375rem;
              font-weight: 400;
            }
            &__sender {
              display: none;
            }
          }
          &__status {
            display: none;
          }
        }
        &__sender_df {
          margin-top: 12px;
          flex-direction: row;
          justify-content: flex-start;
          width: 100%;
          font-size: 0.875rem;
          span {
            margin-left: 50px;
            font-size: 0.875rem;
          }
        }
        &__text {
          margin-top: 7px;
        }
        &__status {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 13px;
          &__date {
            font-size: 0.875rem;
          }
        }
        &__inner-review {
          padding: 20px 5px 0 37px;
          &__head {
            display: flex;
            &__img-wrapper {
              width: 78px;
              height: 23px;
            }
            &__title {
              padding-left: 15px;
            }
            &__sender {
              font-size: 0.9375rem;
              font-weight: 400;
            }
            &__date {
              font-size: 0.75rem;
            }
          }
        }
      }
    }
  }
}

.feedback {
  background: #f9f9f9;
  &__form {
    .form {
      .input {
        width: 100%;
      }
      textarea {
        height: 120px;
      }
      &__fields {
        &__inner {
          display: flex;
          justify-content: space-between;
          .form {
            &__field {
              width: calc(33.33% - 15px);
            }
          }
        }
      }
      &__field {
        margin-bottom: 24px;
      }
      &__footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 10px;
        .form {
          &__field {
            margin-bottom: 0;
          }
        }
      }
      &__stars {
        display: flex;
        align-items: center;
        &__title {
          margin-right: 35px;
          color: #979797;
          font-size: 1.0625rem;
          font-weight: 500;
        }
      }
    }
  }
}

@include sm-down {
  .feedback {
    &__form {
      .form {
        textarea.input {
          margin-bottom: 6px;
        }
        &__fields {
          &__inner {
            .form {
              &__field {
                width: 100%;
              }
            }
          }
        }
        &__field {
          margin-bottom: 15px;
          width: 100%;
        }
        &__footer {
          margin-top: 0;
          .btn {
            margin: 15px auto 0;
          }

        }
        &__stars {
          flex-wrap: nowrap;
          justify-content: space-between;
          width: 100%;
        }
      }
    }
  }
}