/* Settings for site */
* {
  box-sizing: border-box;
  flex-wrap: wrap;
  font-family: $base-font;
}

html {
  font-size: 16px;
}

body {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  min-height: 100%;
  min-width: 320px;
  width: 100%;
  color: $base-color;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0.01em;
  &.overflow{
    overflow: hidden;
  }
  footer {
    margin-top: auto;
  }
}

@include md-down {
  body {
    padding-top: 43px;
  }
}

@supports (-webkit-overflow-scrolling: touch) {
  body {
    cursor: pointer;
  }
}

section, header, footer, main {
  max-width: 100%;
}

.wrapper {
  max-width: 100%;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 998;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.5);
  opacity: 0;
  visibility: hidden;
  transition: .3s;
  &_visible {
    opacity: 1;
    visibility: visible;
  }
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

video {
  pointer-events: none;
}

button, input, textarea {
  background: none;
  border: none;
}

textarea {
  resize: none;
  overflow-y: auto;
}

a {
  color: inherit;
  text-decoration: none;
  transition: all 0.2s;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 300;
}

svg {
  fill: currentColor;
}

.svg-sprite {
  display: none;
}

.icon {
  max-width: 100%;
  max-height: 100%;
  &-arrow {
    color: transparent;
  }
}


.container {
  &_parts {
    position: relative;
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1195px;
    width: 100%;
  }
  &_section {
    position: relative;
    padding: 0 35px;
    max-width: 1205px;
    width: 100%;
    &-wrapper {
      position: relative;
      margin: 0 auto;
      padding: 95px 5px 100px;
      max-width: 1205px;
      width: 100%;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 5px;
        width: 1px;
        height: 100%;
        background: #d1d1d1;
      }
    }
  }
}

@include md-down {
  .container {
    &_section {
      padding: 0 10px;
      max-width: 1024px;
      &-wrapper {
        padding: 60px 5px 50px;
        &:before {
          display: none;
        }
      }
    }
  }
}

@include sm-down {
  .container {
    &_section {
      padding: 0 5px;
      &-wrapper {
        padding: 50px 5px;
      }
    }
  }
}

.icon {
  max-width: 100%;
  max-height: 100%;
  &-arrow {
    color: transparent;
  }
}

.btn {
  display: inline-block;
  &_default {
    padding: 6px 26px;
    font-size: 0.875rem;
    font-weight: 500;
    border: 2px solid #86BE5C;
    border-radius: 4px;
    &:hover {
      color: #ffffff;
      background: #86be5c;
    }
    &_cl {
      padding: 7px 26px;
      font-size: 0.875rem;
      font-weight: 500;
      border: 2px solid #86BE5C;
      border-radius: 20px;
      &:hover {
        color: #ffffff;
        background: #86be5c;
      }
    }
  }
  &_main {
    padding: 15px 25px;
    color: #ffffff;
    font-size: 1.25rem;
    font-weight: 700;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    background: #86BE5C;
    border-radius: 7px;
    &:hover {
      background: darken(#86BE5C, 5%);
    }
    .icon-arrow {
      margin-left: 2px;
      stroke: #ffffff;
      width: 38px;
      height: 12px;
    }
    &_light {
      padding: 15px 25px;
      color: #86BE5C;
      font-size: 1.25rem;
      font-weight: 700;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      background: #ffffff;
      border-radius: 7px;
      .icon-arrow {
        margin-left: 7px;
        stroke: #86BE5C;
        width: 38px;
        height: 12px;
      }
    }
  }
  &_info {
    padding: 12px 20px;
    color: #ffffff;
    font-weight: 700;
    text-transform: uppercase;
    border: 1px solid #ffffff;
    border-radius: 5px;
    &:hover {
      color: $base-color;
    }
    &_full-bg {
      padding: 12px 20px;
      color: #ffffff;
      font-weight: 700;
      text-transform: uppercase;
      background: #86be5c;
      border-radius: 5px;
      &:hover {
        color: $base-color;
      }
    }
  }
  &_back {
    padding: 15px 25px;
    color: #FFFFFF;
    font-size: 1.25rem;
    font-weight: 700;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    background: #86BE5C;
    border-radius: 7px;
    .icon-arrow {
      margin-right: 7px;
      stroke: #FFFFFF;
      width: 38px;
      height: 12px;
      transform: rotate(180deg);
    }
  }
}

@include lg-down {
  .btn {
    &_default {
      padding: 6px 20px;
    }
  }
}

@include md-down {
  .btn {
    &_main {
      &_light {
        padding: 14px 25px;
        font-size: 1rem;
        .icon {
          &-arrow {
            width: 27px;
          }
        }
      }
    }
  }
}

@include sm-down {
  .btn {
    &_main {
      padding: 14px;
      font-size: 1.125rem;
      .icon-arrow {
        width: 28px;
      }
    }
    &_back {
      padding: 14px 20px;
      font-size: 0.875rem;
    }
  }
}

.input {
  position: relative;
  background: #ffffff;
  &_default {
    padding: 9px 10px 9px 40px;
    width: 100%;
    font-size: 1.0625rem;
    font-weight: 500;
    border-radius: 5px;
    &::placeholder {
      color: #979797;
    }
  }
  &_main {
    padding: 9px 21px;
    font-size: 1.0625rem;
    font-weight: 500;
    border: 1px solid #DFDFDF;
    border-radius: 5px;
    &::placeholder {
      color: #979797;
    }
  }
}

@include sm-down {
  .input {
    &_main {
      padding: 9px 16px;
      font-size: 1rem;
    }
  }
}

@include md-up {
  .logo {
    &-mob {
      display: none;
    }
  }
}

@include md-down {
  .logo {
    &-desk {
      display: none;
    }
  }
}

.main-nav {
  nav > ul {
    > li {
      position: relative;
      &::before {
        content: "";
        position: absolute;
        right: 1px;
        top: 50%;
        transform: translateY(-50%);
        height: 16px;
        width: 1px;
        background: #E9E9E9;
      }
      &:last-of-type {
        &::before {
          display: none;
        }
      }
    }
  }
  ul {
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      flex-grow: 1;
      padding: 11px 0;
      a {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        color: #585858;
        font-size: 0.75rem;
        font-weight: 600;
        text-transform: uppercase;
        letter-spacing: 0.04em;
        &:hover {
          color: darken(#cecece, 15%);
        }
      }
    }
  }
  .menu-item-has-children {
    position: relative;
    .icon {
      margin-right: 2px;
      width: 16px;
      height: 10px;
      color: transparent;
      stroke: #cecece;
      stroke-width: 2px;
    }
    ul {
      display: none;
    }
  }
}

@include md-up {
  .main-nav {
    .menu-item-has-children {
      display: flex;
      justify-content: center;
      align-items: center;
      > a {
        padding-right: 16px;
      }
      ul {
        position: absolute;
        overflow-y: auto;
        top: 100%;
        left: -30px;
        width: 200px;
        max-height: 310px;
        background: #ffffff;
        z-index: 999;
        li {
          padding: 0;
          a {
            display: block;
            text-overflow: ellipsis;
            overflow: hidden;
            padding: 11px 5px;
            text-align: center;
            &:hover {
              background: darken(#ffffff, 1%);
            }
          }
        }
      }
    }
  }
}

@include md-down {
  .main-nav {
    ul {
      li {
        &:before {
          display: none;
        }
        .icon {
          position: absolute;
          top: 14px;
          right: 10px;
          width: 12px;
          height: 8px;
        }
      }
    }
  }
}

.link {
  &_default {
    display: flex;
    align-items: center;
    color: #86be5c;
    font-weight: 600;
    &:hover {
      color: darken(#86be5c, 15%);
      .icon-arrow {
        stroke: darken(#86be5c, 15%);
      }
    }
    .icon-arrow {
      margin-left: 10px;
      stroke: #86BE5C;
      width: 38px;
      height: 13px;
    }
  }
}

@include sm-down {
  .link {
    &_default {
      font-size: 0.875rem;
    }
  }
}

.section {
  &-banner {
    position: relative;
    overflow: hidden;
    background: url("../img/bg/bg-section-banner.jpg") 50% 0 no-repeat;
    background-size: cover;
    color: #ffffff;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(#202020, 0.7);
      z-index: 1;
    }
    .container_section {
      &-wrapper {
        padding: 75px 5px;
      }
    }
    .section {
      &__title {
        font-size: 2.8125rem;
      }
    }
    &__inner {
      position: relative;
      z-index: 2;
      &::before, &::after {
        content: '';
        position: absolute;
        width: 252px;
        height: 252px;
        border-radius: 100%;
      }
      &::before {
        top: 0;
        left: 0;
        transform: translate(-50%, -50%);
        border: 58px solid #86BE5C;
      }
      &::after {
        bottom: 0;
        right: 0;
        transform: translate(50%, 50%);
        border: 28px solid #86BE5C;
      }
    }
    .container_section {
      &-wrapper {
        position: relative;
        z-index: 3;
      }
    }
    .section {
      &__head {
        margin-bottom: 0;
      }
    }
  }
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;
  }
  &__title {
    position: relative;
    font-size: 2.5rem;
    font-family: $secondary-font;
    font-weight: 800;
    span {
      font-weight: 400;
    }
    &_bd {
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: -35px;
        transform: translateY(-50%);
        height: 100%;
        width: 8px;
        background: #86be5c;
      }
    }
  }
  &_action {
    padding: 40px 0;
    background: url("../img/bg/bg-section_action.png") 50% 50% no-repeat;
    .section {
      &__head {
        margin-bottom: 0;
      }
      &__title {
        color: #ffffff;
      }
    }
    .container_section-wrapper {
      padding: 0;
      &:before {
        display: none;
      }
    }
    &__inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &_gray {
    background: #F9F9F9;
  }
}

@media screen and (max-width: 1640px) {
  .section {
    &-banner {
      &__inner {
        &::before, &::after {
          display: none;
        }
      }
    }
  }
}

@include lg-down {
  .section {
    &_action {
      background-position: 55% 50%;
      .section {
        &__head {
          margin-bottom: 30px;
        }
      }
    }
  }
}

@include md-down {
  .section {
    &-banner {
      .container_section {
        &-wrapper {
          padding: 25px 5px;
        }
      }
      .section {
        &__title {
          font-size: 1.375rem;
        }
      }
    }
    &_action {
      padding: 35px 0 40px;
      background: url("../img/bg/bg-section_action_green-part.png") 50% 50% no-repeat;
      background-size: cover;
      .section {
        &__title {
          text-align: center;
          font-size: 1.375rem;
        }
      }
      &__inner {
        flex-direction: column;
      }
    }
    &__head {
      margin-bottom: 30px;
    }
    &__title {
      font-size: 1.375rem;
      &_bd {
        padding-left: 5px;
        &:before {
          left: -5px;
          width: 5px;
          top: calc(50% - 3px);
          height: calc(100% + 6px);
        }
      }
    }
    &_gray {
      background: #ffffff;
    }
  }
}

.table_default {
  overflow: hidden;
  border-radius: 6px;
  .table {
    &__info, &__val {
      padding: 12px 30px;
    }
    &__info {
      width: 70%;
    }
    &__val {
      width: 30%;
    }
    &__item {
      display: flex;
    }
    &__head {
      color: #ffffff;
      font-size: 1.25rem;
      font-weight: 700;
      background: #86BE5C;
      .table {
        &__info, &__val {
          padding: 16px 30px;
        }
        &__info {
          border-right: 1px solid #AACE8F;
        }
      }
    }
    &__body {
      font-size: 1.125rem;
      font-weight: 500;
      .table {
        &__val {
          background: rgba(#E9E9E9, 0.4);
        }
        &__item {
          background: #FBFBFB;
          &:nth-of-type(odd) {
            background: #F5F5F5;
          }
        }
      }
    }
  }
}

@include md-up {
  .table_default {
    .table {
      &__body {
        .table {
          &__val {
            &__name {
              display: none;
            }
          }
        }
      }
    }
  }
}

@include md-down {
  .table_default {
    .table {
      &__info, &__val {
        padding: 10px 12px;
        width: 100%;
      }
      &__head {
        font-size: 1rem;
        .table {
          &__info, &__val {
            padding: 12px;
          }
          &__val {
            display: none;
          }
        }
      }
      &__body {
        font-size: 1rem;
        .table {
          &__val {
            display: flex;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-items: center;
            background: #E9E9E9;
            &__name {
              color: #8a8a8a;
              font-size: 0.875rem;
            }
          }
          &__item {
            &:nth-of-type(odd) {
              background: #FBFBFB;
            }
          }
        }
      }
    }
  }
}

.error-mg {
  display: none;
  margin-top: 5px;
  font-size: 0.75rem;
  color: #e12121;
}

.field-radio {
  cursor: pointer;
  margin-right: 28px;
  &:last-of-type {
    margin-right: 0;
  }
  input {
    display: none;
  }
  span {
    position: relative;
    padding-left: 36px;
    &:before, &:after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      border-radius: 100%;
    }
    &:before {
      width: 20px;
      height: 20px;
      background: #ffffff;
      border: 1px solid #86BE5C;
    }
    &:after {
      display: none;
      left: 4px;
      width: 12px;
      height: 12px;
      background: #86be5c;
    }
  }
  input[type="radio"]:checked + span:after {
    display: block;
  }
}

.form {
  &__field {
    &.val_empty {
      .error-mg {
        display: block;
      }
    }
  }
  &_default {
    width: 337px;
    max-width: 100%;
    .form {
      &__field {
        position: relative;
        margin-bottom: 15px;
        &:last-of-type {
          margin-bottom: 0;
        }
        &:before {
          content: '';
          position: absolute;
          top: 11px;
          left: 15px;
          width: 15px;
          height: 15px;
          z-index: 1;
        }
        &-name {
          &:before {
            background: url("../img/icons/icon-user.png") 50% 50% no-repeat;
            background-size: contain;
          }
        }
        &-phone {
          &:before {
            background: url("../img/icons/icon-phone.png") 50% 50% no-repeat;
            background-size: contain;
          }
        }
        &-topic {
          &:before {
            background: url("../img/icons/icon-conversation.png") 50% 50% no-repeat;
            background-size: contain;
          }
        }
        &-message {
          &:before {
            background: url("../img/icons/icon-letter.png") 50% 50% no-repeat;
            background-size: contain;
          }
        }
      }
      &__action {
        margin-top: 15px;
        .btn {
          width: 100%;
        }
      }
    }
  }
}

.main-controls-item {
  display: flex;
  flex-wrap: nowrap;
  &__icon-wrapper {
    width: 32px;
    height: 32px;
    .icon {
      width: 32px;
      height: 32px;
      color: #86be5c;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    padding-left: 15px;
    width: calc(100% - 32px);
    max-width: 220px;
    font-size: 0.9375rem;
    font-weight: 500;
    a {
      &:hover {
        font-weight: 600;
      }
    }
  }
  &_more {
    align-items: flex-start;
    .main-controls-item__content {
      &__title {
        margin-bottom: 10px;
        font-size: 1.25rem;
        font-weight: 600;
      }
    }
  }
}

@include md-down {
  .main-controls-item {
    &__content {
      &_more {
        .main-controls-item__content {
          &__title {
            margin-bottom: 5px;
            font-size: 1rem;
          }
        }
      }
    }
  }
}

@include lg-down {
  .main-controls-item {
    &__content {
      padding-left: 10px;
      font-size: 0.875rem;
    }
  }
}

.slider {
  &_main  {
    &-wrapper {
      .slick {
        &-dots {
          display: flex;
          justify-content: center;
          margin-top: 30px;
          padding: 0 70px;
          li {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 10px;
            overflow: hidden;
            width: 25px;
            height: 25px;
            border-radius: 100%;
            border: 1px solid #86BE5C;
            &:hover {
              background: #f1f1f1;
            }
            button {
              width: 17px;
              height: 17px;
              color: transparent;
              background: transparent;
              border-radius: 100%;
              &::before {
                display: none;
              }
            }
          }
          .slick {
            &-active {
              &:hover {
                background: transparent;
              }
              button {
                background: #86BE5C;
              }
            }
          }
        }
      }
    }
  }
  &__arrow {
    &_main {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      bottom: 0;
      width: 50px;
      height: 50px;
      border: 1px solid #AACE8F;
      border-radius: 4px;
      cursor: pointer;
      z-index: 2;
      &:hover {
        background: #AACE8F;
        .icon {
          stroke: #ffffff;
        }
      }
      .icon {
        width: 15px;
        height: 15px;
        stroke: #8a8a8a;
        stroke-width: 2;
      }
      &-prev {
        left: 0;
        .icon {
          transform: rotate(90deg);
        }
      }
      &-next {
        right: 0;
        .icon {
          transform: rotate(-90deg);
        }
      }
    }
  }
  &__dots {
    &_main {
      &.swiper-pagination-bullets {
        display: flex;
        justify-content: center;
        margin-top: 30px;
        padding: 0 70px;
        .swiper-pagination-bullet {
          display: block;
          margin: 0 10px;
          overflow: hidden;
          width: 24px;
          height: 24px;
          background: #ffffff;
          border-radius: 100%;
          border: 1px solid #86BE5C;
          opacity: 1;
          cursor: pointer;
          &:hover {
            background: #f1f1f1;
          }
          &-active {
            position: relative;
            background: #ffffff;
            &:hover {
              background: transparent;
            }
            &::before {
              content: '';
              display: block;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              width: 16px;
              height: 16px;
              background: #86BE5C;
              border-radius: 100%;
            }
          }
        }
      }
    }
  }
}

@include md-down {
  .slider {
    &_main {
      &-wrapper {
        .slick {
          &-dots {
            margin-top: 10px;
            padding: 0;
          }
        }
      }
    }
    &__arrow {
      &_main {
        width: 142px;
        &-prev {
          left: 0;
        }
        &-next {
          right: 0;
        }
      }
    }
    &__dots {
      &_main {
        &.swiper-pagination-bullets {
          margin-top: 10px;
          padding: 0;
        }
      }
    }
  }
}

.info-block {
  display: flex;
  position: relative;
  margin-bottom: 20px;
  overflow: hidden;
  height: 205px;
  background: #ffffff;
  border-radius: 7px;
  &_news {
    flex-direction: column;
    flex-wrap: nowrap;
    height: 430px;
    .info-block {
      &__img-wrapper {
        width: 100%;
        height: 205px;
      }
      &__desc {
        width: 100%;
        &__text {
          max-height: 110px;
        }
      }
    }
  }
  &__img-wrapper {
    width: 190px;
    overflow: hidden;
    a {
      display: block;
      width: 100%;
      height: 100%;
      max-height: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__desc {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 20px 20px 20px 12px;
    width: calc(100% - 190px);

    img, video, picture {
      display: none;
    }

    &__title {
      font-weight: 600;
      a {
        &:hover {
          color: #8a8a8a;
          text-decoration: underline;
        }
      }
    }
    &__text {
      margin-top: 12px;
      max-height: 100px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    &__status {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: auto;
      color: #8a8a8a;
      font-size: 0.875rem;
      > div {
        display: flex;
        align-items: center;
      }
      .icon {
        margin-right: 9px;
        width: 18px;
        height: 18px;
        color: #86BE5C;
      }
    }
  }
}

@include md-up {
  .info-block {
    &__desc {
      &__more {
        display: none;
      }
    }
  }
}

@include md-down {
  .info-block {
    height: auto;
    &_articles {
      background: none;
      .info-block {
        &__desc {
          padding: 15px 4px 20px;
        }
      }
    }
    &_news {
      height: auto;
      .info-block {
        &__img-wrapper {
          height: 145px;
        }
        &__desc {
          font-size: 0.875rem;
          &__text {
            max-height: none;
          }
          &__status {
            margin-top: 12px;
            font-size: 0.75rem;
          }
        }
      }
    }
    &__img-wrapper {
      width: 100%;
      height: 165px;
    }
    &__desc {
      padding: 15px 12px 20px;
      width: 100%;
      &__more {
        margin-top: 12px;
        a {
          justify-content: flex-end;
        }
      }
      &__status {
        margin-top: 12px;
      }
    }
  }
}

.shadow {
  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 56px;
    background: linear-gradient(360deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
  }
}

@include md-down {
  .more-text-js {
    position: relative;
    overflow: hidden;
    max-height: 215px;
    &.full {
      max-height: 100%;
    }
  }
}

.sort-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: 1.125rem;
    font-weight: 500;
    &__title {
      margin-right: 18px;
      color: #8a8a8a;
    }
    &__kinds {
      position: relative;
      &__current {
        display: block;
        position: relative;
        padding: 0 35px 5px 0;
        overflow: hidden;
        width: 350px;
        white-space: nowrap;
        text-overflow: ellipsis;
        border-bottom: 2px solid #86BE5C;
        user-select: none;
        cursor: pointer;
        .icon {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: 10px;
          width: 11px;
          height: 9px;
          stroke: #8A8A8A;
        }
      }
      ul {
        position: absolute;
        overflow-y: auto;
        top: calc(100% + 10px);
        left: 0;
        width: 100%;
        max-height: 150px;
        background: #ffffff;
        z-index: 2;
        li {
          padding: 5px 10px;
          cursor: pointer;
          &:hover {
            background: darken(#fff, 10%);
          }
        }
      }
    }
  }
}

@include md-down {
  .sort-block {
    &__item {
      margin-bottom: 20px;
      font-size: 1rem;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}

@include sm-down {
  .sort-block {
    &__item {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 20px;
      width: 100%;
      &:last-of-type {
        margin-bottom: 0;
      }
      > * {
        width: 100%;
      }
      &__title {
        margin-bottom: 12px;
      }
    }
  }
}

@include xs-down {
  .sort-block {
    &__item {
      &__kinds {
        &__current {
          width: 100%;
        }
      }
    }
  }
}

.pg-tl {
  .section {
    &-banner {
      background: url("../img/bg/bg-section-banner-inner.png") 50% 50% no-repeat;
      background-size: cover;
    }
  }
}

.btn-top {
  display: none;
  position: fixed;
  bottom: 20px;
  left: 20px;
  width: 30px;
  height: 30px;
  z-index: 100;
  .icon {
    fill: $base-color;
  }
}

@include md-up {
  .btn-top {
    width: 45px;
    height: 45px;
  }
}

.btn-top.show {
  display: block;
}

.statusListKinds {
  display: none;
}

.alignleft {
  float: left;
}

.alignright {
  float: right;
}

.aligncenter {
  display: flex;
  margin-left: auto;
  margin-right: auto;
}