.pg-news {
  background: #f9f9f9;
  .section {
    &-banner {
      background: url("../img/bg/bg-section-banner-inner.png") 50% 50% no-repeat;
      background-size: auto auto;
    }
  }
  &__st {
    &__inner {
      padding-top: 60px;
    }
    &__items {
      margin-top: 90px;
    }
    &__item {
      .info-block {
        min-height: 166px;
        height: auto;
        &__img-wrapper {
          width: 300px;
        }
        &__desc {
          width: calc(100% - 300px);
          &__text {
            margin-top: 8px;
            line-height: 1.5;
          }
          &__more {
            display: block;
            position: absolute;
            bottom: 20px;
            right: 25px;
            .link_default {
              justify-content: flex-end;
            }
          }
          &__status {
            justify-content: flex-start;
            & > * {
              margin-right: 100px;
              &:last-of-type {
                margin-right: 0;
              }
            }
          }
        }
      }
    }
  }
}

@include md-down {
  .pg-news {
    &__st {
      &__inner {
        padding-top: 20px;
      }
      &__items {
        margin-top: 50px;
      }
      &__item {
        .info-block {
          &__img-wrapper {
            width: 100%;
          }
          &__desc {
            width: 100%;
            &__more {
              position: static;
            }
            &__status {
              justify-content: space-between;
              font-size: 0.75rem;
              & > * {
                margin-right: 0;
              }
            }
          }
        }
      }
    }
  }
}

@include sm-down {
  .pg-news {
    &__st {
      &__item {
        .info-block {
          &__desc {
            &__text {
              max-height: 250px;
            }
          }
        }
      }
    }
  }
}