.pg-contact {
  .section {
    &-banner {
      background: url("../img/bg/bg-contact.png") 50% 50% no-repeat;
      background-size: auto auto;
    }
  }
}

.contact {
  position: relative;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    background: #F9F9F9;
  }
  > .container_section {
    &-wrapper {
      padding: 0;
      height: 100%;
      &:before {
        display: none;
      }
    }
  }
  .container_section {
    &-wrapper {
      height: 100%;
    }
  }
  .section {
    &__title {
      margin-bottom: 50px;
      font-size: 1.75rem;
    }
  }
  &__inner {
    display: flex;
  }
  &__data {
    position: relative;
    width: 50%;
    .bread-crumbs {
      position: absolute;
      top: 0;
      left: 0;
    }
    &__info {
      .main-controls-item {
        align-items: center;
        margin-bottom: 25px;
        &:last-of-type {
          margin-bottom: 0;
        }
        &__img-wrapper {
          width: 37px;
          height: 37px;
        }
        &__content {
          width: calc(100% - 37px);
          max-width: 100%;
          font-size: 1.25rem;
          &_more {
            font-size: 1rem;
          }
        }
        &_more {
          align-items: flex-start;
        }
      }
      &__call {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 25px;
        .main-controls-item {
          &__content {
            white-space: nowrap;
          }
        }
      }
    }
  }
  &__form {
    width: 50%;
    .container_section {
      &-wrapper {
        padding-left: 0;
        &:before {
          display: none;
        }
      }
    }
    .form {
      width: 100%;
      textarea {
        height: 207px;
      }
      &__fields {
        &__inner {
          display: flex;
          justify-content: space-between;
          .form {
            &__field {
              width: calc(50% - 5px);
            }
          }
        }
      }
      &__action {
        margin-left: auto;
        width: 170px;
      }
    }
  }
}

@include md-down {
  .contact {
    &::before {
      display: none;
    }
    .section {
      &__title {
        margin-bottom: 30px;
        font-size: 1.375rem;
      }
    }
    &__data {
      width: 100%;
      > .container_section {
        &-wrapper {
          padding-top: 75px;
        }
      }
      &__info {
        .main-controls-item {
          margin-bottom: 13px;
          &__img-wrapper {
            width: 30px;
            height: 30px;
          }
          &__content {
            width: calc(100% - 30px);
            font-size: 0.875rem;
            &_more {
              font-size: 0.875rem;
            }
          }
        }
        &__call {
          justify-content: flex-start;
          margin-bottom: 13px;
          .btn {
            margin-left: 35px;
            padding: 7px 13px;
            font-size: 0.75rem;
          }
        }
      }
    }
    &__form {
      width: 100%;
      background: #F9F9F9;
      .container_section {
        &-wrapper {
          padding-left: 5px;
        }
      }
      .form {
        textarea {
          height: 150px;
        }
        &__fields {
          &__inner {
            margin-bottom: 15px;
            .form {
              &__field {
                width: 100%;
              }
            }
          }
        }
        &__action {
          margin-right: auto;
          margin-left: auto;
          width: 290px;
        }
      }
    }
  }
}

@include xs-down {
  .contact {
    &__data {
      &__info {
        &__call {
          justify-content: space-between;
          .btn {
            margin-left: 0;
          }
        }
      }
    }
  }
}