.pg-service-ct {
  .tl-text {
    .container_section {
      &-wrapper {
        padding-bottom: 50px;
      }
    }
  }
}

@include md-down {
  .pg-service-ct {
    .tl-text {
      .container_section {
        &-wrapper {
          padding-top: 25px;
          padding-bottom: 25px;
        }
      }
    }
  }
}

.we-chose {
  overflow: hidden;
  .container_section {
    &-wrapper {
      padding-top: 50px;
    }
  }
  &__items {
    &-wrapper {
      margin-left: -24px;
      &__inner {
        width: 2000px;
        overflow: hidden;
      }
      .slick {
        &-list {
          width: var(--widthSlider);
          overflow: visible !important;
        }
        &-slide {
          margin-left: 23px;
          opacity: 0.6;
          &.slick-active {
            opacity: 1;
          }
          & > div {
            height: 100%;
          }
        }
      }
    }
  }
  &__item {
    position: relative;
    overflow: hidden;
    padding: 22px 20px 30px 28px;
    height: 100%;
    border: 1px solid #CECECE;
    border-bottom: 6px solid #86BE5C;
    border-radius: 8px;
    &__text {
      position: relative;
      font-size: 1.125rem;
      font-weight: 500;
      z-index: 2;
      &__title {
        margin-bottom: 14px;
        font-size: 1.1875rem;
        font-weight: 700;
      }
    }
    &__number {
      &-bg {
        position: absolute;
        display: inline-block;
        left: -65px;
        bottom: -28px;
        color: rgba(196, 196, 196, 0.2);
        font-size: 12.5rem;
        font-weight: 800;
        line-height: 1;
      }
    }
  }
}

@include md-up {
  .we-chose {
    &__items {
      &-wrapper {
        .slider {
          &__arrow {
            &_main {
              top: -4px;
              &-prev {
                left: auto;
                right: 105px;
              }
              &-next {
                right: 35px;
              }
            }
          }
        }
      }
    }
  }
}

@include md-down {
  .we-chose {
    .container_section {
      &-wrapper {
        padding-top: 25px;
      }
    }
    &__items {
      &-wrapper {
        position: relative;
        padding-bottom: 65px;
        .slick {
          &-list {
            width: 100% !important;
          }
        }
        &__inner {
          width: 100%;
        }
        .slider {
          &__arrow {
            &_main {
              &-prev {
                left: 23PX;
              }
            }
          }
        }
      }
    }
  }
}


@include sm-down {
  .we-chose {
    &__item {
      display: block !important;
      margin: auto;
      padding: 16px 15px 40px 16px;
      width: 295px !important;
      &__text {
        &__title {
          font-size: 1rem;
        }
        font-size: 1rem;
      }
    }
  }
}

.warranty {
  overflow: hidden;
  .section {
    &__title {
      font-size: 1.25rem;
    }
  }
  .container_section {
    &-wrapper {
      padding-top: 0;
      padding-bottom: 0;
    }
  }
  &__inner {
    display: flex;
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-right: 65px;
    padding-top: 30px;
    width: calc(100% - 300px);
    &__info {
      display: flex;
      & > * {
        margin-bottom: 35px;
      }
      .main-controls-item {
        align-items: center;
        &:nth-of-type(odd) {
          padding-right: 35px;
          width: 59%;
        }
      }
      &__call {
        display: flex;
        justify-content: space-between;
        flex-wrap: nowrap;
        white-space: nowrap;
        padding-right: 35px;
        width: 59%;
        .main-controls-item {
          margin-right: 32px;
          &:nth-of-type(odd) {
            width: auto;
            padding-right: 0;
          }
        }
      }
    }
  }
  &__map {
    width: 300px;
    .map-wrapper {
      width: 100%;
    }
  }
}

@include lg-down {
  .warranty {
    &__content {
      padding-right: 30px;
    }
  }
}

@include md-down {
  .warranty {
    .section {
      &__title {
        font-size: 1rem;
      }
    }
    .container_section {
      &-wrapper {
        padding-top: 25px;
      }
    }
    &__content {
      padding-top: 0;
      padding-right: 0;
      width: 100%;
      &__info {
        width: 100%;
        .main-controls-item {
          &:nth-of-type(odd) {
            padding-right: 35px;
            width: 100%;
          }
        }
        &__call {
          width: 100%;
          justify-content: flex-start;
          .main-controls-item {
            &:nth-of-type(odd) {
              padding-right: 0;
              width: auto;
            }
          }
        }
      }
    }
    &__map {
      margin-top: 15px;
      width: 100%;
      .map-wrapper {
        width: 100%;
        height: 318px;
      }
    }
  }
}

@include sm-down {
  .warranty {
    &__content {
      &__info {
        > * {
          margin-bottom: 15px;
        }
        .main-controls-item {
          &__icon-wrapper {
            .icon {
              width: 30px;
              height: 30px;
            }
          }
          &__content {
            padding-left: 3px;
          }
        }
        &__call {
          justify-content: space-between;
          padding-right: 0;
          .main-controls-item {
            margin-right: 0;
          }
          .btn {
            padding: 7px 13px;
            font-size: 0.75rem;
          }
        }
      }
    }
  }
}