.footer {
  display: flex;
  flex-direction: column;
  margin-bottom: -32px;
  font-size: 0.9375rem;
  font-weight: 500;
  &__info {
    padding: 40px 0 20px;
    background: #F4F4F4;
    &__inner {
      display: flex;
    }
    &__item {
      padding: 0 75px;
      width: 37%;
      border-right: 1px solid #CECECE;
      &:first-of-type {
        padding-left: 0;
        width: 26%;
      }
      &:last-of-type {
        border-right: none;
        padding-right: 0;
      }
      &__title {
        margin-bottom: 20px;
        font-size: 1.125rem;
        font-weight: 700;
      }
      .main-controls-item {
        margin-bottom: 25px;
        &:last-of-type {
          margin-bottom: 0;
        }
        &__icon-wrapper {
          min-width: 32px;
        }
      }
      &-services {
        padding-right: 50px;
        ul {
          li {
            margin-bottom: 15px;
            &:last-of-type {
              margin-bottom: 0;
            }
            a {
              &:hover {
                font-weight: 600;
              }
            }
          }
        }
        &__list {
          display: none;
          &:first-of-type {
            display: block;
          }
        }
      }
      &-controls {
        &__buttons-wrapper {
          .btn {
            width: 100%;
            text-align: center;
          }
        }
      }
    }
  }
  &__copy {
    padding: 9px 0;
    background: #e9e9e9;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__item {
      font-weight: 500;
      &-social {
        ul {
          display: flex;
          li {
            margin-right: 25px;
            &:last-of-type {
              margin-right: 0;
            }
            a {
              display: flex;
              justify-content: center;
              align-items: center;
              width: 30px;
              height: 30px;
              &:hover {
                opacity: 0.8;
              }
            }
          }
        }
      }
      &-logo-rocket-cn {
        display: flex;
        align-items: center;
        font-size: 1.125rem;
        color: #b2b2b2;
        a {
          display: inline-block;
          margin: 8px 0 0 10px;
        }
      }
    }
  }
}

@include md-down {
  .footer {
    .main-nav {
      padding-top: 10px;
      background: #f4f4f4;
      .menu-item-has-children {
        display: none;
      }
      ul {
        li {
          width: 50%;
          a {
            justify-content: flex-start;
            color: #434242;
            &:before {
              display: none;
            }
          }
        }
      }
    }
    &__info {
      padding: 20px 0 0;
      &__item {
        margin-bottom: 20px;
        padding: 0;
        width: 100%;
        border-right: none;
        &:first-of-type {
          width: 100%;
        }
        .main-controls-item {
          &__icon-wrapper {
            .icon {
              width: 30px;
              height: 30px;
            }
          }
        }
        &-services {
          display: flex;
          &__list {
            display: block;
            margin-bottom: 10px;
            padding-right: 5px;
            width: 50%;
          }
        }
        &-form {
          display: none;
        }
        &-controls {
          margin-bottom: 0;
          &__buttons-wrapper {
            display: none;
          }
        }
        &__title {
          margin-bottom: 8px;
          font-size: 0.875rem;
        }
      }
    }
  }
}

@include sm-down {
  .footer {
    &__copy {
      padding: 15px 0;
      &__inner {
        flex-direction: column;
        align-items: center;
      }
      &__item {
        margin-top: 15px;
        &:first-of-type {
          margin-top: 0;
        }
        &-logo-rocket-cn {
          display: none;
        }
      }
    }
  }
}