.bread-crumbs {
  .container_section {
    &-wrapper {
      padding-top: 20px;
      padding-bottom: 0;
    }
  }
  ul {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    li {
      position: relative;
      margin-right: 14px;
      padding-right: 22px;
      font-size: 0.875rem;
      font-weight: 500;
      &:last-child {
        margin-right: 0;
        &::after {
          display: none;
        }
      }
      a {
        color: #8A8A8A;
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  ul {
    li {
      &::after {
        content: '>';
        position: absolute;
        top: 0;
        right: 0;
        width: 8px;
        height: 8px;
        font-size: 0.875rem;
        font-weight: 600;
        color: #CECECE;
      }
    }
  }
}

@include sm-down {
  .bread-crumbs {
    ul {
      li {
        margin-right: 18px;
        padding-right: 26px;
        font-size: 0.75rem;
      }
    }
  }
}