.error-st {
  text-align: center;
  background: #f9f9f9;
  &__info {
    margin-top: -50px;
    h2 {
      font-size: 1.875rem;
      font-weight: 600;
    }
    &__buttons-wrapper {
      margin-top: 30px;
    }
  }
}

@include sm-down {
  .error-st {
    text-align: center;
    background: #f9f9f9;
    &__info {
      margin-top: 0;
      h2 {
        font-size: 1.375rem;
      }
      &__buttons-wrapper {
        margin-top: 30px;
      }
    }
  }
}