.pg-articles {
  background: #FBFBFB;
  .section {
    &-banner {
      background: url("../img/bg/bg-articles.png") 50% 60% no-repeat;
      background-size: auto auto;
    }
  }
}

.st-articles {
  > .container_st {
    &-wrapper {
      padding-top: 50px;
    }
  }
  &__items {
    display: flex;
    margin-top: 80px;
  }
  &__item {
    margin-right: 22px;
    width: calc(33.33% - 15px);
    &:nth-of-type(3n) {
      margin-right: 0;
    }
    .info-block {
      &_news {
        height: 470px;
        .info-block {
          &__desc {
            &__text {
              position: relative;
              max-height: 150px;
            }
          }
        }
      }
    }
  }
}

@include md-down {
  .st-articles {
    &__items {
      margin-top: 40px;
    }
    &__item {
      .info-block {
        &__desc {
          &__status {
            margin-top: auto;
          }
        }
      }
    }
  }
}

@include sm-down {
  .st-articles {
    &__item {
      margin-right: 15px;
      width: calc(50% - 15px);
      &:nth-of-type(2n) {
        margin-right: 0;
      }
      &:nth-of-type(3n) {
        margin-right: 15px;
      }
      .info-block {
        &_news {
          height: auto;
          .info-block {
            &__desc {
              &__text {
                margin: 6px 0 10px;
              }
            }
          }
        }
      }
    }
  }
}

@include xs-down {
  .st-articles {
    &__items {
      flex-direction: column;
      align-items: center;
    }
    &__item {
      margin-right: 0;
      width: 300px;
      max-width: 100%;
      &:nth-of-type(3n) {
        margin-right: 0;
      }
    }
  }
}