.pg-sl-services {
  .section {
    &-banner {
      background: url("../img/bg/bg-section-banner-inner.png") 50% 0 no-repeat;
      background-size: cover;
    }
  }
}

.repair {
  &__items {
    display: flex;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px 2px 25px;
    width: calc(25% - 15px);
    margin-right: 20px;
    margin-bottom: 20px;
    border: 1px solid #DFDFDF;
    border-radius: 6px;
    &:nth-of-type(4n) {
      margin-right: 0;
    }
    &__img-wrapper {
      height: 128px;
      img {
        max-height: 100%;
      }
    }
    &__title {
      margin-top: 11px;
      text-align: center;
      font-size: 1.125rem;
      font-weight: 500;
    }
  }
}

@include md-down {
  .repair {
    &__item {
      width: calc(33.33% - 14px);
      &:nth-of-type(4n) {
        margin-right: 20px;
      }
      &:nth-of-type(3n) {
        margin-right: 0;
      }
    }
  }
}

@include sm-down {
  .repair {
    &__item {
      width: calc(50% - 10px);
      &:nth-of-type(3n) {
        margin-right: 20px;
      }
      &:nth-of-type(2n), &:nth-of-type(4n) {
        margin-right: 0;
      }
      &__img-wrapper {
        height: 115px;
      }
    }
  }
}

@include xs-down {
  .repair {
    &__item {
      width: 100%;
      margin-right: 0;
      &:nth-of-type(3n) {
        margin-right: 0;
      }
      &__img-wrapper {
        height: 115px;
      }
    }
  }
}