.pg-sl-pare-parts {
  .tl-text {
    .container_section {
      &-wrapper {
        padding-top: 50px;
        padding-bottom: 70px;
      }
    }
  }
  &__top-desc.tl-text {
    .container_section {
      &-wrapper {
        padding-bottom: 50px;
      }
    }
  }
}

@include md-down {
  .pg-sl-pare-parts {
    .tl-text {
      .container_section {
        &-wrapper {
          padding-top: 20px;
          padding-bottom: 30px;
        }
      }
    }
    &__top-desc.tl-text {
      .container_section {
        &-wrapper {
          padding-top: 30px;
          padding-bottom: 10px;
        }
      }
    }
  }
}

.sl-spare-parts {
  .container_section {
    &-wrapper {
      padding-top: 10px;
    }
  }
  &__items {
    display: flex;
  }
  &__item {
    display: flex;
    justify-content: center;
    margin-right: 25px;
    margin-bottom: 25px;
    padding: 25px 29px 29px;
    width: calc(25% - 20px);
    height: 255px;
    text-align: center;
    border: 1px solid #838383;
    border-bottom: 4px solid #86BE5C;
    border-radius: 6px;
    &:nth-of-type(4n) {
      margin-right: 0;
    }
    &__img-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      height: 110px;
      width: 100%;
      img {
        height: auto;
        width: auto;
        max-height: 100%;
      }
    }
    &__title {
      overflow: hidden;
      text-overflow: ellipsis;
      max-width: 100%;
      width: 100%;
      height: 43px;
      font-size: 1.125rem;
      font-weight: 500;
    }
    .btn {
      margin-top: 5px;
      padding: 8px 15px;
      font-size: 1.125rem;
      font-weight: 500;
      text-transform: none;
    }
  }
}

@include md-down {
  .sl-spare-parts {
    .container_section {
      &-wrapper {
        padding-top: 25px;
      }
    }
    &__item {
      width: calc(33.33% - 17px);
      &:nth-of-type(4n) {
        margin-right: 25px;
      }
      &:nth-of-type(3n) {
        margin-right: 0;
      }
      &__title {
        font-size: 1rem;
      }
      .btn {
        font-size: 1rem;
      }
    }
  }
}

@include sm-down {
  .sl-spare-parts {
    &__item {
      width: calc(50% - 13px);
      &:nth-of-type(3n) {
        margin-right: 25px;
      }
      &:nth-of-type(2n), &:nth-of-type(4n) {
        margin-right: 0;
      }
    }
  }
}

@include es-down {
  .sl-spare-parts {
    &__items {
      flex-direction: column;
      align-items: center;
    }
    &__item {
      margin-right: 0;
      width: 100%;
      max-width: 270px;
      &:nth-of-type(3n) {
        margin-right: 0;
      }
    }
  }
}