.tl-text {
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.5;
  &_info {
    h2 {
      font-size: 2.8125rem;
    }
  }
  h2 {
    font-size: 1.875rem;
    font-weight: 800;
  }
  h3 {
    font-size: 1.5625rem;
    font-weight: 700;
  }
  h4, h5, h6 {
    font-weight: 600;
  }
  a {
    font-style: italic;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
  p {
    margin-bottom: 15px;
  }
  ul {
    li {
      position: relative;
      padding-left: 45px;
      margin-bottom: 15px;
      font-weight: 600;
      &::before {
        content: url("data:image/svg+xml; utf8, %3Csvg width='15' height='12' viewBox='0 0 15 12' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2.40526 3.27632L8.32582 9.47437L5.92056 11.9924L0 5.79432L2.40526 3.27632Z' fill='%2386BE5C'/%3E%3Cpath d='M3.53725 9.482L12.5947 0L15 2.518L5.94252 12L3.53725 9.482Z' fill='%2386BE5C'/%3E%3C/svg%3E");
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  .container_section {
    &-wrapper {
      padding-top: 85px;
    }
  }
  .wp-block-image {
    .alignleft {
      float: left;
      margin-right: 4.2em;
    }
  }
}

@include md-up {
  .tl-text {
    .container_section {
      &-wrapper {
        padding-bottom: 90px;
      }
    }
  }
}

@include md-down {
  .tl-text {
    font-size: 1rem;
    h2 {
      font-size: 1.375rem;
    }
    h3 {
      font-size: 1.125rem;
    }
    ul {
      li {
        padding-left: 25px;
        &::before {
          content: url("data:image/svg+xml; utf8, %3Csvg width='12' height='12' viewBox='0 0 15 12' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2.40526 3.27632L8.32582 9.47437L5.92056 11.9924L0 5.79432L2.40526 3.27632Z' fill='%2386BE5C'/%3E%3Cpath d='M3.53725 9.482L12.5947 0L15 2.518L5.94252 12L3.53725 9.482Z' fill='%2386BE5C'/%3E%3C/svg%3E");
        }
      }
    }
    .container_section {
      &-wrapper {
        padding-top: 50px;
      }
    }
    .wp-block-image {
      text-align: center;
      .alignleft {
        float: none;
      }
    }
  }
}

@include sm-down {
  .tl-text {
    .container_section {
      &-wrapper {
        padding: 35px 5px 25px;
      }
    }
  }
}