.pg-vacancies {
  .section {
    &-banner {
      background: url("../img/bg/bg-vacancies.png") 50% 0 no-repeat;
      background-size: auto auto;
    }
  }
}

.vacancies {
  .container_section {
    &-wrapper {
      padding-top: 40px;
    }
  }
  &__desc {
    font-size: 1.125rem;
  }
  &__items {
    display: flex;
    margin-top: 85px;
  }
  &__item {
    margin: 0 23px 23px 0;
    padding: 23px 19px;
    overflow: hidden;
    width: calc(33.33% - 16px);
    border: 1px solid #DFDFDF;
    border-radius: 7px;
    &:nth-of-type(3n) {
      margin-right: 0;
    }
    .icon {
      margin-right: 15px;
      color: #86be5c;
      width: 15px;
      height: 15px;
    }
    &__title {
      font-size: 1.125rem;
      font-weight: 600;
    }
    &__price {
      margin-top: 16px;
      font-weight: 500;
    }
    &__action {
      display: inline-block;
      margin-top: 18px;
      font-size: 0.9375rem;
      font-weight: 500;
      &:hover {
        text-decoration: underline;
      }
    }
    &__info {
      margin-top: 13px;
      span {
        font-weight: 500;
      }
    }
    &__status {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 16px;
      padding-right: 15px;
      color: #8a8a8a;
      font-size: 0.875rem;
      > div {
        display: flex;
        align-items: center;
      }
      .icon {
        width: 20px;
        height: 20px;
      }
    }
  }
}

@include md-down {
  .vacancies {
    .container_section {
      &-wrapper {
        padding-top: 15px;
      }
    }
    &__desc {
      font-size: 1rem;
    }
    &__items {
      margin-top: 30px;
    }
    &__item {
      width: calc(50% - 23px);
      &:nth-of-type(2n) {
        margin-right: 0;
      }
      &:nth-of-type(3n) {
        margin-right: 23px;
      }
      &__status {
        padding-right: 0;
      }
    }
  }
}

@include sm-down {
  .vacancies {
    &__item {
      width: 100%;
      margin-right: 0;
      &:nth-of-type(3n) {
        margin-right: 0;
      }
      &__price {
        margin-top: 8px;
      }
      &__action {
        margin-top: 12px;
      }
      &__info {
        margin-top: 17px;
      }
      &__status {
        margin-top: 20px;
      }
    }
  }
}