.modal {
  position: relative;
  display: none;
  margin-top: auto;
  margin-bottom: auto;
  opacity: 0;
  visibility: hidden;
  transition: .3s;
  &_visible {
    display: block;
    opacity: 1;
    visibility: visible;
  }
  &-wrapper {
    position: fixed;
    display: flex;
    justify-content: center;
    overflow-x: hidden;
    overflow-y: auto;
    padding-top: 30px;
    padding-bottom: 30px;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    opacity: 0;
    visibility: hidden;
    z-index: 999;
    &_visible {
      opacity: 1;
      visibility: visible;
    }
  }
  &__inner {
    position: relative;
    padding: 31px 33px 39px;
    width: 400px;
    max-width: 100%;
    background: #FFFFFF;
    border: 1px solid #ffffff;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
  }
  &__title {
    text-align: center;
    font-size: 1.5rem;
    font-weight: 800;
  }
  &__close {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 20px;
    right: 20px;
    width: 26px;
    height: 26px;
    border-radius: 100%;
    .icon {
      width: 100%;
      height: 100%;
      color: #86BE5C;
      &:hover {
        color: darken(#86BE5C, 10%);
      }
    }
  }
  &_default {
    position: relative;
    padding: 11px;
    max-width: 100%;
    .form {
      margin: 35px auto 0;
      width: 330px;
      max-width: 100%;
      &__field {
        margin-bottom: 15px;
        &:last-of-type {
          margin-bottom: 0;
        }
        .input {
          padding: 8px 17px;
          width: 100%;
        }
      }
      &__action {
        margin-top: 15px;
        .btn {
          padding: 8px 26px;
          width: 100%;
        }
      }
    }
  }
  &_thanks {
    .modal {
      &__inner {
        padding: 40px;
        border: 1px solid #86BE5C;
      }
      &__title {
        font-size: 1.0375rem;
        font-weight: 500;
        line-height: 1.5;
      }
    }
  }
}

@include sm-down {
  .modal {
    &__inner {
      padding: 25px 20px 30px;
    }
    &_default {
      .form {
        margin-top: 25px;
      }
    }
  }
}