.main {
  overflow: hidden;
  position: relative;
  background: url("../img/bg/bg-main.jpg") 50% 50% no-repeat;
  .container_section-wrapper {
    padding-top: 105px;
    padding-bottom: 185px;
  }
  .section {
    &__title {
      font-size: 2.8125rem;
    }
  }
  &:after {
    content: '';
    position: absolute;
    top: -40px;
    left: calc(50% - 520px);
    width: 200px;
    height: 200px;
    border: 2px dashed #0088CC;
    border-radius: 100%;
    opacity: 0.4;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__details {
    li {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
      font-size: 1.25rem;
      font-weight: 700;
      .icon {
        &-wrapper {
          display: flex;
          justify-content: center;
          align-items: center;
          margin-right: 25px;
          padding: 10px;
          width: 60px;
          height: 60px;
          border: 3px solid #86BE5C;
          border-radius: 100%;
        }
        width: 29px;
        height: 29px;
        color: #8a8a8a;
      }
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
  &__buttons-wrapper {
    margin-top: 55px;
  }
}

@include md-down {
  .main {
    .section {
      &__title {
        font-size: 1.375rem;
      }
    }
  }
}

@include sm-down {
  .main {
    background: url("../../img/bg/bg-banner_gray.png") 50% -1px no-repeat;
    background-size: auto 100%;
    &:before {
      top: auto;
      left: auto;
      bottom: 0;
      right: -45px;
      width: 133px;
      height: 133px;
    }
    &:after {
      top: 65px;
      left: -23px;
      width: 65px;
      height: 65px;
    }
    .container_section-wrapper {
      padding-top: 50px;
      padding-bottom: 40px;
    }
    &__inner {
      position: relative;
      &:before {
        content: '';
        position: absolute;
        top: -113px;
        right: -82px;
        width: 120px;
        height: 120px;
        border: 13px solid #AACE8F;
        border-radius: 100%;
      }
    }
    &__details {
      li {
        font-size: 1.125rem;
        .icon {
          width: 24px;
          height: 24px;
          &-wrapper {
            margin-right: 20px;
            width: 50px;
            height: 50px;
          }
        }
      }
    }
  }
}

.services {
  &__items {
    display: flex;
  }
  &__item {
    margin-bottom: 25px;
    margin-right: 18px;
    width: calc(33.33% - 13px);
    height: 215px;
    &:nth-of-type(3n + 3) {
      margin-right: 0;
    }
    a {
      display: block;
      width: 100%;
      height: 100%;
      &:hover {
        .services__item {
          &__inner {
            background: #F5F5F5;
          }
        }
      }
    }
    &__inner {
      position: relative;
      width: 100%;
      height: 100%;
      padding: 30px;
      background: #FDFDFD;
      border: 1px solid #CECECE;
      border-bottom: 6px solid #86BE5C;
      border-radius: 10px;
      &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 155px;
        height: 155px;
        background: url("../img/icons/icon-cogwheel.png") no-repeat;
        opacity: 0.2;
      }
    }
    &__img-wrapper {
      position: relative;
      .icon {
        width: 74px;
        height: 62px;
      }
    }
    &__desc {
      position: relative;
      margin-top: 24px;
      color: #747474;
      span {
        display: block;
        color: $base-color;
        font-size: 1.25rem;
        font-weight: 700;
      }
    }
  }
}

@include md-up {
  .services {
    margin-top: -40px;
  }
}

@include md-down {
  .services {
    &__item {
      &__inner {
        padding: 25px 22px;
        &:before {
          width: 125px;
          height: 125px;
          background-size: cover;
        }
      }
      &__desc {
        span {
          font-size: 1.125rem;
        }
      }
    }
  }
}

@include sm-down {
  .services {
    &__items {
      flex-direction: column;
      align-items: center;
    }
    &__item {
      margin-bottom: 15px;
      margin-right: 0;
      width: 100%;
      max-width: 300px;
      height: 176px;
      &:nth-of-type(3n + 3) {
        margin-right: 0;
      }
      &__inner {
        padding: 18px 10px 22px 20px;
      }
      &__img-wrapper {
        width: 60px;
        height: 60px;
      }
      &__desc {
        margin-top: 10px;
      }
    }
  }
}

@include md-down {
  .support {
    &__form-wrapper {
      width: 100%;
      .form {
        margin: 0 auto;
      }
    }
  }
}

.about {
  overflow: hidden;
  &__title {
    margin-bottom: 35px;
    font-size: 1.5rem;
    font-weight: 600;
  }
  &__content {
    display: flex;
  }
  &__desc {
    padding-right: 30px;
    width: calc(100% - 265px);
    &__info {
      font-size: 1.125rem;
      line-height: 2;
      &__more {
        display: none;
        text-align: right;
        font-size: 0.875rem;
        color: #b3b3b3;
        font-weight: 500;
        &:hover {
          text-decoration: underline;
        }
        &.show {
          display: block;
        }
        .icon-arrow {
          margin-left: 2px;
          transform: rotate(-90deg);
          width: 10px;
          height: 8px;
          stroke: #b3b3b3;
          stroke-width: 2px;
        }
      }
    }
    &__advantages {
      display: flex;
      margin-top: 30px;
      &__item {
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        padding-right: 5px;
        width: 30%;
        min-width: 195px;
        &:nth-of-type(2) {
          width: 40%;
        }
        &__img-wrapper {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 50px;
          height: 50px;
          border: 2px solid #86BE5C;
          border-radius: 100%;
          .icon {
            color: #8a8a8a;
            width: 25px;
            height: 25px;
          }
        }
        &__title {
          padding-left: 15px;
          width: calc(100% - 50px);
          font-size: 1.25rem;
          font-weight: 700;
        }
      }
    }
  }
  &__license {
    margin-top: -50px;
    width: 265px;
    &__slider {
      &-wrapper {
        position: relative;
        padding-bottom: 12px;
        .slick {
          &-dots {
            margin-top: 40px;
            padding: 0 56px;
            max-width: 262px;
          }
        }
      }
    }
    &__slide {
      max-width: 100%;
    }
    &__arrow {
      &-next {
        right: 0;
      }
    }
  }
}

@include md-up {
  .about {
    &__license {
      &__slider {
        &-wrapper {
          &__inner {
            width: 675px;
            overflow: hidden;
          }
          .slick {
            &-list {
              overflow: visible;
              width: 262px !important;
            }
            &-slide {
              margin-right: 30px;
              opacity: 0.4;
            }
            &-current {
              opacity: 1;
            }
          }
        }
      }
    }
  }
}

@include md-down {
  .about {
    &__title {
      margin-bottom: 15px;
      font-size: 1rem;
    }
    &__desc {
      padding-right: 0;
      width: 100%;
      &__info {
        font-size: 1rem;
        &__text {
          line-height: 1.5;
        }
      }
      &__advantages {
        &__item {
          margin-bottom: 20px;
          width: 100%;
          &:nth-of-type(2) {
            width: 100%;
          }
          &:last-of-type {
            margin-bottom: 0;
          }
          &__img-wrapper {
            width: 44px;
            height: 44px;
          }
          &__title {
            font-size: 1rem;
          }
        }
      }
    }
    &__license {
      margin-top: 45px;
      width: 100%;
      &__slider {
        &-wrapper {
          .slick {
            &-dots {
              margin-top: 20px;
              padding: 0 147px;
              max-width: 100%;
            }
          }
        }
      }
      &__slide {
        img {
          margin: auto;
        }
      }
    }
  }
}

@include sm-down {
  .about {
    &__license {
      &__slider {
        &-wrapper {
          padding-bottom: 60px;
          .slick {
            &-list {
              margin: auto;
            }
            &-dots {
              margin-top: 10px;
              padding: 0;
              max-width: 100%;
            }
          }
        }
      }
    }
  }
}

.trust {
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  min-height: 220px;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: calc(50% + 115px);
    height: 100%;
    width: 50%;
    background: url("../img/bg/bg-section_action_dark-part.png") 0 50% no-repeat;
    background-size: 100% 100%;
    z-index: 2;
  }
  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: calc(50% + 150px);
    height: 100%;
    background: #fff url("../img/bg/bg-section_action_gray-part.png") 0 50% no-repeat;
    background-size: 100% 100%;
    z-index: 1;
  }
  .section {
    &__head {
      margin-bottom: 0;
    }
  }
  &__inner {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    margin: 0 auto;
    padding: 0 35px;
    max-width: 1175px;
    width: 100%;
    z-index: 3;
  }
  &__title {
    position: relative;
    color: #ffffff;
    max-width: 445px;
    z-index: 4;
  }
  &__items {
    margin-left: -100px;
    margin-right: -100px;
    display: flex;
    flex-wrap: nowrap;
    &-wrapper {
      position: absolute;
      overflow: hidden;
      padding: 37px 50% 37px 100px;
      top: 50%;
      left: calc(50% - 35px);
      transform: translateY(-50%);
      z-index: 3;
    }
    &__column {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 155px;
      &:last-of-type {
        .trust__item {
          margin-right: 80px;
        }
      }
    }
  }
  &__item {
    margin: 0 30px 30px 0;
    min-width: 150px;
    height: calc(155px / 2 - 30px);
    &:last-of-type {
      margin-bottom: 0;
    }
    img {
      width: auto;
    }
  }
}

@include md-down {
  .trust {
    padding-top: 20px;
    &:before {
      width: 100%;
      height: 70px;
      right: auto;
      left: 50%;
      transform: translateX(-50%);
      background: url("../img/bg/bg-section_action_dark-part_vertical.png") 0 0 no-repeat;
      background-size: 100% 100%;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50px;
      right: -16%;
      left: auto;
      bottom: 0;
      width: 132%;
      height: 100%;
      background: #fff url("../img/bg/bg-section_action_gray-part_vertical.png") 0 50% no-repeat;
      background-size: 100% 100%;
      z-index: 1;
    }
    .section {
      &__head {
        margin-bottom: 35px;
      }
    }
    &__inner {
      flex-direction: column;
      align-items: center;
      padding: 0 10px;
    }
    &__items {
      flex-wrap: wrap;
      margin: 0;
      &-wrapper {
       position: static;
       padding: 0;
        width: 100%;
       transform: none;
      }
      &__column {
        width: 33.33%;
        height: auto;
        &:nth-of-type(2) {
          .trust__item {
            margin: auto;
            text-align: center;
          }
        }
        &:nth-of-type(5n) {
          .trust__item {
            margin: 0 auto;
            text-align: center;
          }
        }
        &:nth-of-type(3n) {
          .trust__item {
            margin-left: auto;
            text-align: right;
          }
        }
        &:last-of-type {
          .trust__item {
            margin-right: 0;
          }
        }
      }
    }
    &__item {
      margin: 0;
      min-width: auto;
      height: auto;
      text-align: left;
      img {
        width: 100%;
        max-width: 100px;
        margin-bottom: 20px;
      }
    }
  }
}

@include sm-down {
  .trust {
    &__item {
      img {
        max-width: 90px;
      }
    }
  }
}

.prev-st {
  background: #F9F9F9;
  &__inner {
    display: flex;
  }
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;
    .section {
      &__title {
        &_bd {
          margin-bottom: 0;
        }
      }
    }
  }
  &__articles {
    width: 55%;
  }
  &__news {
    padding-left: 100px;
    width: 45%;
  }
}

@include md-up {
  .prev-st {
    .section {
      &__head {
        .icon {
          &-arrow {
            &_short {
              display: none;
            }
          }
        }
      }
    }
  }
}

@include md-down {
  .prev-st {
    .section {
      &__head {
        margin-bottom: 35px;
        .icon {
          &-arrow {
            &_long {
              display: none;
            }
            &_short {
              transform: rotate(-90deg);
              width: 14px;
              height: 18px;
            }
          }
        }
      }
    }
    &__articles {
      margin-bottom: 40px;
      width: 100%;
    }
    &__news {
      padding-left: 0;
      width: 100%;
    }
  }
}