.rating {
  display: flex;
  &-radio {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    flex-wrap: nowrap;
    font-size: 1.875rem;
    input {
      //display: none;
    }
    label {
      display: flex;
      align-items: center;
      color: #C4C4C4;
      cursor: pointer;
    }
    :checked ~ label {
      color: #FFE927  ;
    }
    :hover ~ label {
      color: rgb(250, 176, 5);
    }
  }
  li {
    margin-right: 2px;
    &:last-of-type {
      margin-right: 0;
    }
    .icon {
      width: 18px;
      height: 18px;
      fill: #C4C4C4;;
    }
    &.active {
      .icon {
        fill: #FFE927;
      }
    }
  }
}

@include sm-down {
  .rating {
    &-radio {
      font-size: 1.5625rem;
    }
    li {
      .icon {
        width: 15px;
        height: 15px;
      }
    }
  }
}