.reviews {
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__items {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    padding: 30px;
    width: calc(50% - 8px);
    background: #ffffff;
    border: 1px solid #CECECE;
    border-radius: 6px;
    &__head {
      display: flex;
      &__img-wrapper {
        width: 64px;
        height: auto;
        max-height: 64px;
        img {
          max-height: 100%;
        }
      }
      &__name {
        padding-left: 14px;
        padding-right: 130px;
        width: calc(100% - 64px);
        font-size: 0.9375rem;
      }
    }
    &__text {
      position: relative;
      overflow: hidden;
      margin-top: 15px;
      font-size: 1.125rem;
      line-height: 1.5rem;
      height: 208px;
    }
    &__data {
      margin-top: 23px;
      display: flex;
      justify-content: space-between;
      &__status {
        &__date {
          margin-top: 8px;
          color: #8a8a8a;
          font-size: 0.875rem;
        }
      }
      &__sender {
        text-align: right;
        span {
          display: block;
          margin-top: 6px;
          color: #8a8a8a;
          font-size: 0.9375rem;
        }
      }
    }
  }
}

@include md-up {
  .reviews {
    &__more {
      display: none;
    }
  }
}

@include md-down {
  .reviews {
    .section {
      &__head {
        a {
          display: none;
        }
      }
    }
    &__item {
      margin-bottom: 15px;
      padding: 20px 15px;
      width: 100%;
      &:last-of-type {
        margin-bottom: 0;
      }
      &__head {
        &__name {
          padding-right: 0;
        }
      }
      &__text {
        height: auto;
        max-height: 267px;
      }
      &__data {
        margin-top: 10px;
        &__status {
          &__date {
            margin-top: 0;
            font-size: 0.875rem;
          }
        }
        &__sender {
          font-size: 0.9375rem;
          span {
            margin-top: 3px;
          }
        }
      }
    }
    &__more {
      margin-top: 15px;
      a {
        justify-content: center;
      }
    }
  }
}