.equipment {
  &_full {
    .equipment {
      &__kinds {
        &__content {
          &__items {
            &__slider {
              &-wrapper {
              }
            }
          }
          &__item {
            width: 100%;
            max-width: 100%;
          }
        }
      }
    }
  }
  &_prev {
    .equipment {
      &__kinds {
        &__detailed {
          margin-top: 0;
        }
        &__content {
          margin-top: 0;
          &__items {
            &__list {
              display: flex;
            }
            &__slider {
              &-wrapper {
                display: none;
              }
            }
          }
          &__item {
            &__title {
              text-transform: none;
            }
          }
        }
      }
    }
    &-manufac {
      .container_section {
        &-wrapper {
          padding-top: 5px;
        }
      }
      .equipment {
        &__kinds {
          &__detailed {
            margin-top: 0;
          }
          &__content {
            margin-top: 0;
            .equipment__kinds__content__item {
              padding: 30px 20px 20px;
              min-height: 233px;
              height: auto;
              &__img-wrapper {
                height: 35px;
              }
              &__title {
                padding-top: 15px;
                min-height: 155px;
                height: auto;
                font-size: 0.875rem;
                line-height: 1.4;
                text-transform: none;
              }
            }
          }
        }
      }
    }
    &-sale {
      .equipment {
        &__kinds {
          &__detailed {
            margin-top: 0;
            ul {
              li {
                a {
                  span {
                    display: block;
                    font-size: 1rem;
                  }
                }
              }
            }
          }
          &__content {
            margin-top: 0;
            &__slider {
              &-wrapper {
                position: relative;
                padding-bottom: 13px;
              }
            }
            &__cart {
              display: flex;
              &__img-wrapper {
                width: 372px;
                img {
                  max-width: 100%;
                  max-height: 100%;
                }
                &__main {
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  padding: 20px;
                  height: 340px;
                  border: 1px solid #DFDFDF;
                  border-radius: 3px;
                }
                &__items {
                  display: flex;
                  margin-top: 10px;
                }
                &__item {
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  padding: 7px 10px;
                  margin-right: 15px;
                  width: calc(33.33% - 10px);
                  height: 85px;
                  border: 1px solid #DFDFDF;
                  border-bottom-color: #86BE5C;
                  border-radius: 3px;

                  &:nth-of-type(3n) {
                    margin-right: 0;
                  }
                }
              }
              &__info {
                padding-left: 23px;
                width: calc(100% - 372px);
                font-size: 1.125rem;
                font-weight: 500;
                &__title {
                  margin-bottom: 10px;
                  font-weight: 600;
                }
                &__name {
                  font-size: 1.25rem;
                  font-weight: 700;
                }
                &__charac {
                  margin-top: 10px;
                  &__item {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 15px;
                    &:last-of-type {
                      margin-bottom: 0;
                    }
                    &__val {
                      color: #8a8a8a;
                    }
                  }
                }
                &__components {
                  margin-top: 34px;
                  ul {
                    padding-left: 3px;
                    li {
                      margin-bottom: 15px;
                      &:last-of-type {
                        margin-bottom: 0;
                      }
                      .icon {
                        margin-left: 8px;
                        width: 15px;
                        height: 12px;
                        color: #86be5c;
                      }
                    }
                  }
                }
                &__action {
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  margin-top: 41px;
                  &__price {
                    .equipment__kinds__content__cart__info {
                      &__title {
                        margin-bottom: 5px;
                      }
                    }
                    span {
                      font-size: 1.5rem;
                      font-weight: 600;
                    }
                  }
                  .btn {
                    padding: 12px 44px;
                    font-size: 1.0625rem;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  &__kinds {
    display: flex;
    &.active {
      &_green {
        .equipment__kinds {
          &__main {
            &__item {
              &.active {
                color: #ffffff;
                background: #86be5c;
                .icon {
                  color: #ffffff;
                }
              }
            }
          }
          &__detailed {
            ul {
              li {
                &.active {
                  &:before {
                    background: #86be5c;
                  }
                }
              }
            }
          }
          &__content {
            &__items {
              &__slider {
                &-wrapper {
                  .slider {
                    &__dots_main {
                      .swiper-pagination-bullet {
                        border: 1px solid #86be5c;
                        &-active {
                          &::before {
                            background: #86be5c;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      &_blue {
        .equipment__kinds {
          &__main {
            &__item {
              &.active {
                color: #ffffff;
                background: #52ADE1;
                .icon {
                  color: #ffffff;
                }
              }
            }
          }
          &__content {
            &__items {
              &__slider {
                &-wrapper {
                  .slider {
                    &__dots_main {
                      .swiper-pagination-bullet {
                        border: 1px solid #52ade1;
                        &-active {
                          &::before {
                            background: #52ade1;
                          }
                        }
                      }
                    }
                    &__arrow {
                      &_main {
                        border: 1px solid #52ade1;
                        &:hover {
                          background: #52ade1;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          &__detailed {
            ul {
              li {
                &.active {
                  &:before {
                    background: #52ADE1;
                  }
                }
              }
            }
          }
          &__content {
            &__head {
              a {
                color: #52ADE1;
                .icon {
                  stroke: #52ADE1;
                }
              }
            }
            &__item {
              border-bottom: 5px solid #52ADE1;
            }
          }
        }
      }
    }
    &__main {
      width: 100%;
      &__items {
        display: flex;
        justify-content: space-between;
      }
      &__item {
        display: flex;
        align-items: center;
        padding: 5px 20px;
        width: calc(25% - 7px);
        height: 50px;
        color: #747474;
        font-size: 1.25rem;
        font-weight: 600;
        border: 1px solid #86BE5C;
        border-radius: 4px;
        &:nth-of-type(even) {
          border-color: #52ADE1;
          .icon {
            color: #52ade1;
          }
        }
        &:hover {
          background: darken(#fff, 5%);
        }
        .icon {
          margin-right: 17px;
          width: 24px;
          height: 24px;
          color: #86BE5C;
        }
        img {
          margin-right: 17px;
          width: 24px;
          height: 24px;
          &:last-of-type {
            display: none;
          }
        }
        &.active {
          img {
            &:first-of-type {
              display: none;
            }
            &:last-of-type {
              display: block;
            }
          }
        }
      }
    }
    &__detailed {
      margin-top: 50px;
      padding-right: 10px;
      width: 230px;
      ul {
        li {
          position: relative;
          margin-bottom: 25px;
          color: #8A8A8A;
          font-size: 1.25rem;
          font-weight: 600;
          &:last-of-type {
            // margin-bottom: 0;
          }
          &::before {
            content: '';
            position: absolute;
            top: -2px;
            left: -35px;
            width: 6px;
            height: calc(100% + 4px);
            background: #C5C5C5;
          }
          &.active {
            color: #555555;
            &:before {
              background: #86be5c;
            }
          }
          a {
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
      }
    }
    &__content {
      margin-top: 50px;
      width: calc(100% - 230px);
      &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        h4 {
          color: #555555;
          font-size: 1.25rem;
          font-weight: 600;
        }
      }
      &__items {
        &__slider {
          &-wrapper {
            position: relative;
            padding-bottom: 12px;
          }
        }
      }
      &__item {
        display: block;
        padding: 20px;
        width: 273px;
        max-width: 273px;
        height: 255px;
        text-align: center;
        border: 1px solid #CECECE;
        border-bottom: 5px solid #86BE5C;
        border-radius: 6px;
        &__img-wrapper {
          display: flex;
          justify-content: center;
          align-items: center;
          overflow: hidden;
          text-align: center;
          height: 160px;
          a {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
          }
          img {
            height: auto;
            width: auto;
            max-height: 100%;
          }
        }
        &__title {
          overflow: hidden;
          padding-top: 5px;
          height: 60px;
          font-weight: 500;
          letter-spacing: 0.04em;
          text-transform: uppercase;
          text-overflow: ellipsis;
        }
      }
      &__manufacturers {
        margin-top: 30px;
        &__items {
          display: flex;
          align-items: center;
          img {
            margin-right: 15px;
            max-width: 135px;
            max-height: 100px;
          }
        }
      }
    }
    &__manufactures {
      &__head {
        color: #555555;
        font-size: 1.25rem;
        font-weight: 600;
      }
      &__items {
        display: flex;
        margin-top: 25px;
      }
      &__item {
        display: flex;
        align-items: center;
        margin-right: 35px;
        margin-bottom: 25px;
        height: 35px;
        img {
          max-width: 149px;
        }
      }
    }
  }
}

@include md-up {
  .equipment {
    &_prev {
      .equipment__kinds {
        &__content {
          &__item {
            margin-right: 20px;
            margin-bottom: 20px;
            width: calc(33.33% - 25px);
            display: block;
            padding: 20px;
            height: 255px;
            text-align: center;
            border: 1px solid #CECECE;
            border-bottom: 5px solid #86BE5C;
            border-radius: 6px;
          }
        }
      }
    }
    &__kinds {
      &__current-tab {
        display: none;
      }
      &__content {

        &__head {
          .icon {
            &-arrow {
              &-short {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}

@include md-down {
  .equipment {
    &_prev {
      .equipment__kinds {
        &__content {
          margin-top: 20px;
          &__items {
            &__list {
              display: none;
            }
            &__slider {
              &-wrapper {
                display: block;
                padding-bottom: 15px;
                .slider {
                  &__dots {
                    &_main {
                      margin-top: 25px;
                    }
                  }
                }
              }
            }
          }
          &__item {
            margin: 0;
            padding: 20px 48px 35px;
            max-width: none;
            height: 255px;
            &__img-wrapper {
              height: 140px;
              a {
                display: flex;
                justify-content: center;
                max-height: 100%;
              }
            }
            &__title {
              padding-top: 20px;
              height: auto;
              font-size: 0.875rem;
            }
          }
        }
        &__manufactures {
          &__item {
            justify-content: center;
            margin-right: 35px;
            width: calc(33.33% - 35px);
            &:nth-of-type(3n) {
              margin-right: 0;
            }
          }
        }
      }
      &-sale {
        .equipment {
          &__kinds {
            &__content {
              margin-top: 15px;
              &__slider {
                &-wrapper {
                  padding-bottom: 60px;
                }
              }
              &__cart {
                flex-direction: column;
                &__img-wrapper {
                  margin: auto;
                  width: 295px;
                  max-width: 100%;
                  &__main {
                    height: 275px;
                  }
                  &__item {
                    height: 92px;
                  }
                }
                &__info {
                  padding-left: 0;
                  margin-top: 25px;
                  width: 100%;
                  font-size: 1rem;
                  &__charac {
                    &__item {
                      font-size: 1rem;
                    }
                  }
                  &__components {
                    margin-top: 25px;
                  }
                  &__action {
                    margin-top: 20px;
                    &__price {
                      display: flex;
                      justify-content: space-between;
                      align-items: center;
                      width: 100%;
                      font-size: 1.25rem;
                      span {
                        font-size: 1.25rem;
                      }
                    }
                    .btn {
                      margin: 15px auto 0;
                      padding: 12px 100px;
                      max-width: 100%;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    &__kinds {
      &__current-tab {
        position: relative;
        padding: 10px 26px 10px 10px;
        color: #8a8a8a;
        font-size: 0.875rem;
        font-weight: 500;
        border: 1px solid #DFDFDF;
        border-radius: 3px;
        cursor: pointer;
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          right: 10px;
          transform: translateY(-50%);
          border-top: 10px solid #C4C4C4;
          border-right: 7px solid transparent;
          border-left: 7px solid transparent;
        }
        span {
          display: block;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
      &__main {
        position: relative;
        &__items {
          display: none;
          position: absolute;
          top: 100%;
          left: 0;
          height: auto;
          max-height: 250px;
          width: 100%;
          background: #fff;
          border: 1px solid #DFDFDF;
          border-top: none;
          border-radius: 3px;
          z-index: 101;
        }
        &__item {
          height: 35px;
          width: 100%;
          font-size: 0.875rem;
          font-weight: 500;
          border: none;
          border-radius: 0;
          &.active {
            .icon, img, img:last-of-type {
              display: none;
            }
          }
          .icon, img, img:last-of-type {
            display: none;
          }
        }
      }
      &__detailed {
        margin-top: 5px;
        padding-right: 0;
        position: relative;
        width: 100%;
        ul {
          display: none;
          position: absolute;
          top: 100%;
          left: 0;
          height: auto;
          max-height: 250px;
          width: 100%;
          background: #fff;
          border: 1px solid #DFDFDF;
          border-top: none;
          border-radius: 3px;
          z-index: 101;
          li {
            margin-bottom: 0;
            &:before {
              display: none;
            }
            a {
              display: block;
              overflow: hidden;
              padding: 10px;
              font-size: 0.875rem;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
          }
        }
      }
      &__content {
        margin-top: 25px;
        width: 100%;
        &__head {
          margin-bottom: 30px;
          font-size: 0.875rem;
          h4 {
            font-size: 0.875rem;
          }
          .icon {
            margin-left: 10px;
            width: 15px;
            height: 10px;
            stroke: #86BE5C;
            color: transparent;
            stroke-width: 2px;
            transform: rotate(-90deg);
            &-arrow {
              &-long {
                display: none;
              }
            }
          }
        }
        &__items {
          &__slider {
            &-wrapper {
              padding-bottom: 60px;
            }
          }
        }
        &__item {
          margin: auto;
          &__img-wrapper {
            height: 145px;
          }
          &__title {
            padding-top: 10px;
            height: 75px;
            text-transform: none;
          }
        }
        &__manufacturers {
          &__items {
            img {
              margin-right: 5px;
              margin-bottom: 15px;
            }
          }
        }
      }
      &__manufactures {
        &__head {
          margin-top: 30px;
        }
        &__items {
          justify-content: center;
        }
      }
    }
  }
}

@include sm-down {
  .equipment {
    &_prev {
      .equipment {
        &__kinds {
          &__content {
            &__items {
              &__slider {
                &-wrapper {
                  display: block;
                  padding-bottom: 60px;
                }
                display: block;
              }
            }
          }
          &__manufactures {
            &__item {
              width: calc(50% - 35px);
              &:nth-of-type(3n) {
                margin-right: 35px;
              }
              &:nth-of-type(2n) {
                margin-right: 0;
              }
            }
          }
        }
      }
      &-manufac {
        .equipment {
          &__kinds {
            &__content {
              .equipment__kinds__content__item {
                &__title {
                  min-height: auto;
                }
              }
            }
          }
        }
      }
    }
    &__kinds {
      &__content {
        &__head {
          .link_default {
            .icon {
              margin-left: 4px;
            }
          }
        }
        &__item {
          max-width: 100%;
        }
      }
    }
  }
}

@include es-down {
  .equipment {
    &_prev {
      &-manufac {
        .equipment {
          &__kinds {
            &__content {
              .equipment__kinds__content__item {
                padding-bottom: 35px;
                min-height: auto;
              }
            }
          }
        }
      }
    }
  }
}