.pg-about {
  background: #F9F9F9;
}

@include md-down {
  .pg-about {
    background: none;
  }
}

.priorities {
  overflow: hidden;
  &__items {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    display: flex;
    align-items: center;
    padding: 21px 20px;
    width: 331px;
    max-width: 32%;
    background: #FFFFFF;
    border: 1px solid #DFDFDF;
    border-radius: 7px;
    &__img-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 67px;
      height: 67px;
      border: 3px solid #86BE5C;
      border-radius: 100%;
      .icon {
        width: 35px;
        height: 26px;
        fill: #8a8a8a;
      }
    }
    &__title {
      padding-left: 20px;
      width: calc(100% - 67px);
      font-size: 1.25rem;
      font-weight: 700;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
    &__item {
      display: flex;
      flex-direction: column;
      width: 50%;
      &:first-of-type {
        width: calc(50 - 30px);
        padding-right: 30px;
        .priorities__content__item {
          &__desc {
            margin-top: 20px;
          }
        }
      }
      h3, h4 {
        margin-bottom: 20px;
        font-size: 1rem;
        font-weight: 700;
      }
      p {
        margin: 15px 0;
      }
      ul {
        margin: 15px 0;
        padding-left: 40px;
        list-style: disc;
      }
      &__title {
        margin-bottom: 30px;
        font-size: 1.5rem;
        font-weight: 600;
      }
      &__info {
        font-size: 1.125rem;
        line-height: 2;
      }
      &__img-wrapper {
        position: relative;
        height: 439px;
        img {
          position: absolute;
          right: 0;
          height: 100%;
          width: 100%;
          object-fit: cover;
          min-width: 950px;
        }
      }
      .about {
        &__license {
          margin-top: 30px;
          .section__title_bd {
            padding-left: 15px;
            &:before {
              left: 0;
              width: 5px;
            }
          }
        }
      }
    }
  }
}

@include md-up {
  .priorities {
    &__content {
      &__item {
        &__info {
          &__more {
            display: none;
          }
        }
        .about {
          &__license {
            &__slider {
              &-wrapper {
                padding-bottom: 0;

                .slick {
                  &-list {
                    width: 293px!important;
                  }
                }
                &__inner {
                  width: 850px;
                }
              }
            }
            &__slide {
              height: 373px;
              img {
                width: 100%;
                height: 100%;
              }
            }
          }
        }
      }
    }
  }
}

@include md-down {
  .priorities {
    &__items {
      justify-content: center;
    }
    &__item {
      margin-bottom: 10px;
      max-width: 48%;
      &:nth-of-type(even) {
        margin-left: 10px;
      }
    }
    &__content {
      flex-direction: column-reverse;
      margin-top: 20px;
      &__item {
        margin-top: 30px;
        width: 100%;
        &:first-of-type {
          padding-right: 0;
        }
        &__img-wrapper {
          height: 212px;
          img {
            min-width: 100%;
          }
        }
        &__title {
          margin-bottom: 10px;
          font-size: 1rem;
        }
        &__info {
          &__text {
            font-size: 1rem;
          }
        }
        &__info {
          &__more {
            display: block;
            margin-top: 10px;
            text-align: right;
            font-size: .875rem;
            color: #b3b3b3;
            font-weight: 500;
            .icon {
              margin-left: 2px;
              transform: rotate(-90deg);
              width: 10px;
              height: 8px;
              stroke: #b3b3b3;
              stroke-width: 2px;
            }
          }
        }
        .about {
          &__license {
            .section__title_bd {
              padding-left: 0;
              &:before {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}

@include sm-down {
  .priorities {
    &__items {
      flex-direction: column;
      align-items: center;
    }
    &__item {
      max-width: 100%;
      &:nth-of-type(even) {
        margin-left: 0;
      }
    }
  }
}

.why-we {
  background: url("../img/bg/bg-why-we.jpg") 50% 50% no-repeat;
  background-size: cover;
  .container_section {
    &-wrapper {
      padding-bottom: 0;
    }
  }
  &__items {
    &-wrapper {
      .container_section {
        &-wrapper {
          padding: 60px 5px 50px;
        }
      }
    }
    .slick {
      &-list {
        margin-left: -20px;
      }
      &-slide {
        margin-left: 20px;
      }
      &-dots {
        margin-top: 25px;
      }
    }
  }
  &__item {
    position: relative;
    padding: 30px 19px 30px 16px;
    overflow: hidden;
    min-height: 170px;
    background: #FFFFFF;
    border: 1px solid #CECECE;
    border-radius: 8px;
    &__number {
      &-main {
        position: relative;
        display: inline-block;
        color: #86be5c;
        font-size: 1.5rem;
        font-weight: 700;
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: calc(100% + 14px);
          transform: translateY(-50%);
          width: 65px;
          height: 1px;
          background: #8A8A8A;
        }
      }
      &-bg {
        position: absolute;
        color: rgba(196, 196, 196, 0.12);
        font-size: 9.375rem;
        line-height: 1;
        font-weight: 800;
        left: -48px;
        bottom: -22px;
      }
    }
    &__text {
      margin-top: 10px;
      color: $base-color;
      font-size: 1.1875rem;
      font-weight: 600;
    }
  }
}

@include md-down {
  .why-we {
    &__items {
      &-wrapper {
        .container_section {
          &-wrapper {
            padding-top: 30px;
            padding-bottom: 35px;
          }
        }
      }
      .slick {
        &-list {
          padding-left: 0 !important;
        }
      }
    }
    &__item {
      &__text {
        font-size: 1rem;
      }
    }
  }
}

@media screen and (max-width: 399px){
  .why-we {
    &__items {
      &-wrapper {
        .container_section {
          padding-right: 0;
          &-wrapper {
            padding-right: 0;
          }
        }
        .slick {
          &-list {
            margin-left: 0;
          }
          &-slide {
            margin-left: 0;
          }
        }
      }
    }
    &__item {
      padding-right: 22px;
      max-width: 220px;
    }
  }
}

.scheme-work {
  .container_section {
    &-wrapper {
      padding-bottom: 0;
    }
  }
  .section {
    &__head {
      margin-bottom: 0;
    }
  }
  &__items {
    &-wrapper {
      .container_section {
        &-wrapper {
          padding-top: 65px;
        }
      }
    }
    .slick {
      &-list {
        padding-left: 0 !important;
      }
    }
  }
  &__item {
    &__img-wrapper {
      padding-bottom: 35px;
      border-bottom: 1px solid #CECECE;
      .icon {
        width: 30px;
        height: 30px;
        color: #86be5c;
      }
    }
    &__number {
      position: relative;
      display: flex;
      align-items: flex-end;
      padding-top: 5px;
      color: #86be5c;
      font-size: 0.8125rem;
      font-weight: 600;
      line-height: 1;
      &:before {
        content: '';
        position: absolute;
        top: -5px;
        left: 0;
        width: 30px;
        height: 4px;
        background: #86be5c;
      }
      &__total {
        font-size: 0.65rem;
      }
    }
    &__text {
      margin-top: 23px;
      width: 155px;
      max-width: 100%;
      &__title {
        font-size: 1.125rem;
        font-weight: 600;
      }
      &__desc {
        margin-top: 8px;
        font-weight: 500;
      }
    }
  }
}

@include md-down {
  .scheme-work {
    &__items {
      &-wrapper {
        .container_section {
          &-wrapper {
            padding-top: 40px;
          }
        }
      }
    }
  }
}

@include sm-down {
  .scheme-work {
    &__item {
      &__img-wrapper {
        padding-bottom: 25px;
        .icon {
          width: 25px;
          height: 25px;
        }
      }
      &__number {
        &:before {
          width: 25px;
        }
      }
      &__text {
        margin-top: 15px;
        &__title {
          font-size: 1rem;
        }
      }
    }
  }
}

@include es-down {
  .scheme-work {
    &__items {
      &-wrapper {
        .container_section {
          padding-right: 0;
          &-wrapper {
            padding-right: 0;
          }
        }
      }
    }
  }
}