.pg-repair-ar {
  .section {
    &-banner {
      background: url("../img/bg/bg-section-banner-inner.png") 50% 50% no-repeat;
      background-size: auto auto;
      .section {
        &__title {
          line-height: 1;
          span {
            font-size: 1.5rem;
          }
        }
      }
    }
  }
}

@include md-up {
  .pg-repair-ar {
    .tl-text {
      .container_section {
        &-wrapper {
          padding-top: 75px;
        }
      }
    }
  }
}

@include md-down {
  .pg-repair-ar {
    .section {
      &-banner {
        .section {
          &__title {
            span {
              font-size: 0.75rem;
            }
          }
        }
      }
    }
  }
}