.zoom {
  display:none;
  position: fixed;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  height: 90%;
  max-width: max-content;
  max-height: max-content;
  z-index: 999;
  img {
    width: auto !important;
    height: auto !important;
    max-width: 100%;
    max-height: 100%;
    border: 2px solid #d1d1d1;
  }
  &__close {
    position: absolute;
    padding: 0;
    top: 8px;
    right: 10px;
    width: 20px;
    height: 20px;
    color: #000000;
    z-index: 999;
  }
}