.header {
  background: #ffffff;
  position: relative;
  z-index: 4;
  &.active {
    z-index: 999;
  }
  &__prev {
    position: fixed;
    top: -500px;
    left: 0;
    right: 0;
    transition: all 0.3s;
    &.fixed {
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      background: #ffffff;
      z-index: 100;
      border-bottom: 1px solid #cecece;
    }
    &__inner {
      display: flex;
      padding: 0 0 0 4px;
      .main-nav {
        width: calc(100% - 174px);
      }
      .btn {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 36px;
        width: 174px;
        color: #ffffff;
        font-size: 0.875rem;
        background: #86be5c;
        &:hover {
          background: darken(#86be5c, 10%);
        }
      }
    }
  }
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    padding: 25px 0;
    &__logo {
      display: flex;
      align-items: center;
      justify-content: center;
      a {
        display: flex;
        align-items: center;
      }
    }
    &__controls {
      display: flex;
      flex-wrap: nowrap;
      margin-left: auto;
      padding-right: 35px;
      .main-controls-item {
        padding-right: 15px;
        &:last-of-type {
          padding-right: 0;
        }
      }
    }
  }
  &__menu {
    width: 100%;
    &__nav {
      border-top: 1px solid #CECECE;
    }
  }
}

@include lg-down {
  .header {
    &__info {
      &__logo {
        img {
          width: 110px;
        }
      }
      &__controls {
        padding-right: 28px;
      }
      &__action {
        .btn {
          padding: 6px 18px;
        }
      }
    }
  }
}

@include md-up {
  .header {
    &__info {
      &__burger {
        &-wrapper {
          display: none;
        }
      }
      &__action {
        .btn {
          .icon {
            display: none;
          }
        }
      }
    }
    &__menu {
      &__action {
        display: none;
      }
    }
  }
}

@include md-down {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    .container_parts {
      padding: 0;
    }
    &__info {
      padding: 0;
      .main-controls-item {
        &__icon-wrapper {
          width: 28px;
          height: 28px;
        }
        &-schedule {
          display: none;
        }
      }
      &__burger {
        position: relative;
        width: 18px;
        height: 12px;
        cursor: pointer;
        &.open {
          height: 18px;
          span {
            top: calc(50% - 2px);
            width: 18px;
            transform: translateX(-50%) rotate(45deg);
            &:before {
              display: none;
            }
            &:after {
              top: 0;
              transform: rotate(-90deg);
            }
          }
        }
        &-wrapper {
          padding: 0 20px;
        }
        span {
          position: absolute;
          height: 2px;
          width: 14px;
          top: 50%;
          left: calc(50% - 2px);
          transform: translate(-50%, -50%);
          background: #434242;
          &:before, &:after {
            content: '';
            position: absolute;
            left: 0;
            height: 2px;
            width: 18px;
            background: #434242;
          }
          &:before {
            top: -6px;
          }
          &:after {
            top: 6px;
          }
        }
      }
      &__logo {
        padding: 9px;
        border-left: 1px solid #DFDFDF;
        border-right: 1px solid #DFDFDF;
        img {
          width: 90px;
        }
      }
      &__controls {
        margin: auto;
        padding-right: 0;
      }
      &__action {
        .btn {
          padding: 11px 30px;
          font-size: 0;
          color: transparent;
          background: #86be5c;
          border-radius: 0;
          .icon {
            width: 17px;
            height: 17px;
            color: #ffffff;
          }
        }
      }
    }
    &__menu {
      display: none;
      position: absolute;
      background: #ffffff;
      .main-nav {
        ul {
          justify-content: flex-start;
          li {
            padding: 0;
            width: 100%;
            a {
              justify-content: flex-start;
              padding: 12px 14px;
              letter-spacing: 0.04em;
              &:before {
                display: none;
              }
              &:hover {
                background: darken(#ffffff, 1%);
              }
            }
          }
        }
        .menu-item-has-children {
          > a {
            border-bottom: 1px solid #DFDFDF;
          }
        }
      }
      &__action {
        .main-controls-item {
          align-items: center;
          padding: 12px 10px 12px 23px;
          border-top: 1px solid #DFDFDF;
        }
      }
    }
    &__prev {
      &.fixed {
        display: none;
      }
    }
  }
}

@include sm-up {
  .header {
    &__info {
      &__controls {
        &__phone {
          display: none;
        }
      }
    }
  }
}

@include sm-down {
  .header {
    &__info {
      &__logo {
        padding: 11px;
        img {
          width: 80px;
        }
      }
      &__controls {
        .main-controls-item {
          display: none;
        }
        &__phone {
          font-size: 0.75rem;
          font-weight: 500;
          &:hover {
            text-decoration: underline;
          }
        }
      }
      &__action {
        .btn {
          padding: 12px;
        }
      }
    }
  }
}